.chord-content {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 10px;
    box-shadow: 0px 0px 15px 2px rgba(89,86,89,1) !important;
}
.modal {
    height: auto !important;
    top: auto !important;
    left: auto !important;
    bottom: auto !important;
    right: auto !important;
}
.chord-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
}
.chord-list-start {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    margin-top: 40px;
    margin-bottom: 40px;
    -webkit-animation: fade-in 1 1.2s linear;
            animation: fade-in 1 1.2s linear;
}

@-webkit-keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateX(-1500px) scaleX(3);
    }
    50% {
        opacity: 0.5
    }
    100% {
        opacity: 1;
        transform: translateX(0),
    }
}

@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateX(-1500px) scaleX(3);
    }
    50% {
        opacity: 0.5
    }
    100% {
        opacity: 1;
        transform: translateX(0),
    }
}

.loader {
    margin-top: 40px;
}
.description {
    font-size: 15px;
}

.css-1wa3eu0-placeholder {
    color: white !important;
}

.page-active {
    background: violet !important;
    font-weight: bold !important;
}
.fixed {
    margin-bottom: 300px;  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #fdeff9, #ec38bc, #7303c0, #03001e) !important; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.nav-link a:active {
    background: #fff !important;
}

.nav-link:hover {
    transform: scaleX(1.2);
}

.nav-link {
    transition: transform 0.3s ease-out;
}

.results {
    transform: translateY(0);
    visibility: visible;
    opacity: 1;
    z-index: 20 !important;
    width: 22.5%;
    margin-top: 20px;
    background: white;
    border-radius: 10px;
    padding: 15px;
    height: auto;
    position: absolute;
    left: 51%;
}
 

.results h1 {
padding-left: 10px;
  height: 45px;
  line-height: 45px;
}
  





.generator {
    margin-top: 50px;
}
.progression-form {
    padding: 20px;
    margin-bottom: 50px !important;
}

.progression-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin-top: 40px;
    margin-bottom: 40px;
}

.play {
    background: #03001e;  
    background: linear-gradient(to right, #fdeff9, #ec38bc, #7303c0, #03001e);
    -webkit-background-clip: text !important;
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-size: 10px;
    cursor: pointer;
    transition: transform 0.1s;
    transition: background-size 0.5s ease-out;
    margin-bottom: 10px !important;
    background-size: 300%;
    background-position: left;
}

.pause {
    background: #03001e;  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #fdeff9, #ec38bc, #7303c0, #03001e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-background-clip: text !important;
    transition: transform 0.1s;
    transition: background-size 0.5s ease-out;
    background-clip: text;
    -webkit-text-fill-color: transparent; 
    font-size: 30px;
    cursor: pointer;
    background-size: 300%;
    background-position: left;
}

.play:hover {
    transform: translateY(2px);
    background-size: 100%;
}

.pause:hover {
    transform: translateY(2px);
    background-size: 100%;
}


.title {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
